export const setH5PageTouchArea = (top = 0, bottom, height) => {
    try {
        // eslint-disable-next-line no-unused-expressions
        window.grounds?.setH5PageTouchArea && window.grounds.setH5PageTouchArea(top, bottom, height);
    } catch (e) {
        console.log(e);
    }
};

export const setNoSlideArea = (area) => {
    try {
        const data = JSON.stringify({
            area,
            totalHeight: window.innerHeight,
        });

        // eslint-disable-next-line no-unused-expressions
        window?.grounds?.setNoSlideArea && window?.grounds.setNoSlideArea(data);
    } catch (e) {
        console.log(e);
    }
};
/*
window.grounds.openInput(options, callback);
options:
{
    value(string):
    placeholder(string):
    additional(enum):["photo","album","emoji", "gif", "audio"] 
    maxSize(number):
    callback(data):
    text(string):emojigifubbtext
    image(string):()
}
*/
// 
export const showInput = ({ replyName, callback }) => {
    const maxSize = 200;
    const config = {
        placeholder: replyName ? `${replyName}` : '',
        maxSize,
        limit: maxSize,
        additional: ['photo', 'album', 'emoji', 'gif', 'audio'],
    };

    if (isGrounds) {
        // 
        if (userIsLogin()) {
            if (window.grounds.openInput || window.grounds.input) {
                // window
                const groundsCallbackName = 'inputFromClient';

                window[groundsCallbackName] = (data) => {
                    if (data && callback) {
                        callback(data);
                    }
                };

                if (window.grounds.openInput) {
                    window.grounds.openInput(JSON.stringify(config), groundsCallbackName);
                } else {
                    window.grounds.input(JSON.stringify(config), groundsCallbackName);
                }
            } else {
                // 
                // customModal(Input, { callback, config });
            }
        } else {
            userLogin();
        }
    } else {
        // 
    }
};

