export const isCollected = (callback) => {};

// 
/*
documentId, //  {string} id
id, // {string} id
subtype, // {string}  juji
title, //  {string} 
thumbnail,  // {string} 
links : [{ //  {array->object}
    type, // {string} 
    url // {string} 
}],
*/
export const setCollect = ({ documentId, id, subtype, title, thumbnail, links: [{ type, url }] }, callback) => {
    const config = {
        documentId: '', //  {string} id
        id: '', // {string} id
        subtype: '', // {string}  juji
        title: '', //  {string} 
        thumbnail: '', // {string} 
        links: [
            {
                //  {array->object}
                type: '', // {string} 
                url: '', // {string} 
            },
        ],
    };
    // window
    const groundsCallbackName = 'collectClient';

    window[groundsCallbackName] = (data) => {
        if (data && callback) {
            callback(data);
        }
    };

    if (window.grounds.collect) {
        window.grounds.collect(JSON.stringify(config), groundsCallbackName);
    }
};
