import React, { useEffect, useRef, useState, Fragment } from 'react';
import PropTypes from 'prop-types';
import styles from './index.css';
import errorBoundary from "@src/components/errorBoundary"
// 
import { ListStore } from '../../../store';
// 
import { Player } from '@ifeng/three_terminal/es/mobile/video';
// setH5PageTouchArea / setNoSlideArea  setPageNoActiveListener
import { setH5PageTouchArea, setNoSlideArea, setPageNoActiveListener } from '@src/utils/app';
// 
import { isIOS } from '@src/utils';
const PlayerVideo = ({ content }) => {
    // 
    const listStore = ListStore.useContainer();
    const { listStates, changeListStates } = listStore;
    const { currentVideo } = listStates;

    // 
    const videoRef = useRef(); // 
    const videoApis = useRef(); // 
    const videoPosition = useRef(); // 
    const [videoHeight, serVideoHeight] = useState(0);

    useEffect(() => {
        const bodyWidth = document.body.offsetWidth;
        const height = (bodyWidth * 9) / 16;

        serVideoHeight(height);
    }, []);

    // player
    const option = {
        // 
        config: {
            // 
            // width: 325,
            // 
            height: videoHeight,
            // 
            autoPlay: false,
            // 
            controls: true,
            auth: true, // guidfalse
            showTitle: true, // 
            showPoster: true, // 
            showController: true, // 
            m3u8Play: true,
            pauseAfterLeave: true, // 
            singlePlay: true, // 
            isCircle: true,
            controlsList: 'nodownload',
            clickVideoStatisticsCB: () => {
                // console.log('');
            },
            firstFrameStatisticsCB: () => {
                // console.log('');
            },
            // 
            getExposeApisCb: apis => {
                videoApis.current = apis;
            },
            // 
            fullscreenListener: isFull => {
                if (isIOS()) {
                    return;
                }
                if (isFull) {
                    videoPosition.current = document.documentElement.scrollTop || document.body.scrollTop;
                } else {
                    window.scroll(0, videoPosition.current);
                }
            },
        },

        videoinfo: {
            title: currentVideo.title,
            videoPlayUrl: currentVideo.videoPlayUrl,
            bigPosterUrl: currentVideo.posterUrl,
        },
    };

    // 
    useEffect(
        () => {
            if (videoRef.current) {
                const domRect = videoRef.current.getBoundingClientRect();
                const width = domRect.width;
                const height = (width * 9) / 16;

                changeListStates({
                    statusBarHeight: 0,
                    videoHeight: String(Math.floor(height)),
                });
            }
        },
        [videoRef.current],
    );

    return (
        <>
            {currentVideo?.videoPlayUrl ? (
                <div
                    className={styles.top}
                    ref={videoRef}
                    style={{ height: videoHeight }}
                    onClick={() => {
                        const { top, bottom } = videoRef.current?.getBoundingClientRect();

                        setNoSlideArea([{ top, end: bottom, type: 'fixed' }]);
                        setH5PageTouchArea(top, bottom, window.innerHeight);
                    }}>
                    <div id="PlayerWrap">
                        <Player key={currentVideo.base62Id} videoConfig={option.videoinfo} playermsg={option.config} />
                    </div>
                </div>
            ) : null}
        </>
    );
};

PlayerVideo.propTypes = {
    content: PropTypes.object,
};

export default errorBoundary(PlayerVideo);
