// 
// 2021-07-10 13:00:33
// 24XX
// 124XX
// 1XX
// 1
export const formatCommentTime = date => {
    const currentTime = dayjs();
    const targetTime = dayjs(date);
    const ms = targetTime.isAfter(currentTime)
        ? dayjs.duration(targetTime.diff(currentTime))
        : dayjs.duration(currentTime.diff(targetTime));
    const days = Math.floor(ms.asDays());
    const hours = Math.floor(ms.asHours());
    const minutes = Math.floor(ms.asMinutes());

    if (hours >= 24) {
        return `${days}`;
    } else if (hours > 0) {
        return `${hours}`;
    } else if (minutes > 0) {
        return `${minutes}`;
    } else {
        return '';
    }
};
// 
export const i2s = ss => {
    let mm = 0;
    let hh = 0;

    if (ss > 3599) {
        hh = Math.floor(ss / 3600);
        mm = Math.floor((ss % 3600) / 60);
        ss %= 60;

        return `${hh > 9 ? hh : `0${hh}`}:${mm > 9 ? mm : `0${mm}`}:${ss > 9 ? ss : `0${ss}`}`;
    } else if (ss > 59) {
        mm = Math.floor(ss / 60);
        ss %= 60;

        return `${mm > 9 ? mm : `0${mm}`}:${ss > 9 ? ss : `0${ss}`}`;
    } else {
        return `00:${ss > 9 ? ss : `0${ss}`}`;
    }
};
// 
// 2021-07-10 13:00:33
//  07-10
export const formatDateMMDD = str => {
    return dayjs(str).format('MM-DD');
};

// 
// 2021-07-10 13:00:33
//  13:00
export const formatDateHHMM = str => {
    return dayjs(str).format('HH:mm');
};
