const HotComment = ({ content }) => {
    // 
    const useListStore = ListStore.useContainer();
    const { listStates, changeListStates } = useListStore;
    const { currentVideo } = listStates;

    const commentInfo = {
        callType: 'web', // :web/video
        clickCount: {
            behavior: 'action',
            type: 'comtmore',
            other: {
                kind: 'article',
                rel: currentVideo?.commentUrl, // 'ucms_8EiVMbiuE8C'
            },
        },
        cmtLength: 1000,
        cmtNumUrl: 'https://comment.ifeng.com/get.php',
        cmtPostUrl: 'https://comment.ifeng.com/post.php',
        cmtVoteUrl: 'https://comment.ifeng.com/vote.php',
        docTitle: undefined,
        docUrl: currentVideo?.commentUrl, // 'ucms_8EiVMbiuE8C'
        downloads: {
            download: 'https://a.app.qq.com/o/simple.jsp?pkgname=com.ifeng.news2&ckey=CK1354725251042',
            downloadAd: 'https://statistics.appstore.ifeng.com/index.php/api/godownload?app_id=4&d_ch=7641',
        },
        hotLimit: 3,
        id: location.href, // id:8EiVMbiuE8C/location.href (URL)
        isComment: false,
        isVideoShare: undefined,
        jubNum: 0,
        jubUrl: 'https://comment.ifeng.com/report',
        kind: 'share01',
        limitHeight: '',
        pcUrl: `https://v.ifeng.com/c/${currentVideo?.base62Id}`,
        replyLimit: 3,
        skey: currentVideo?.skey,
        speUrl: '',
        wxLaunchSwitch: true,
        wxLaunchType: 'app',
    };

    return (
        <section className={styles.commentBox}>
            <ShareComment articleInfo={commentInfo} />
        </section>
    );
};

HotComment.propTypes = {
    content: PropTypes.object,
};

export default errorBoundary(HotComment);
