import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import styles from './index.css';
import errorBoundary from "@src/components/errorBoundary"
// 
import { ListStore } from '../../store';
// 
import { getRecommend } from '../../services';
import { getlocal } from '@src/utils';
import { message } from '@src/components/ModalBox';

// 
// import { QQShare } from '@ifeng/three_terminal/es/mobile/qqShare/dynamic';
// import { Grounds } from '@ifeng/client_grounds';

const Wrap = content => {
    const {
        account, // 
        dramaSeries, // 
        positive, // 
        related, // 
        crowd, // 
        gags, // 
        params, // id
        query, // 
    } = content.content;

    // 
    const useListStore = ListStore.useContainer();
    const { listStates, changeListStates } = useListStore;
    const { page } = listStates;
    // 
    const initAcount = () => {
        changeListStates({
            account,
        });
    };

    // 
    const initDramaSeries = () => {
        changeListStates({
            dramaSeries,
        });
    };

    // 
    const initPositive = () => {
        const dataList = dramaSeries?.content?.dataList;

        dataList.forEach((item, index) => {
            if (item.extText) {
                positive[index]['extText'] = item.extText;
            }
        });

        changeListStates({
            positive,
        });
    };
    // 
    const initRelationRecommend = () => {
        changeListStates({
            related,
        });
    };

    // 
    const initCrowd = () => {
        changeListStates({
            crowd,
        });
    };

    // 
    const initGags = () => {
        changeListStates({
            gagsList: gags,
        });
    };

    // 
    const initRecommend = () => {
        getRecommend({ docId: dramaSeries.base62Id })
            .then(res => {
                const { data } = res;

                changeListStates({
                    recommond: data.items,
                    page: page + 1,
                });
            })
            .catch(err => {
                console.log('err', err);
            });
    };

    // 
    const inintVideo = () => {
        const {
            source, // 
            positiveId, // ID
            gagsId, // ID
        } = query;

        // 
        if (source) {
            console.log('');
            // url/ID?ID = 1& = positive
            if (source === 'positive' && positiveId) {
                const currentVideoIndex = positive.findIndex(item => item.base62Id === positiveId);

                if (currentVideoIndex > -1) {
                    console.log('');
                    changeListStates({
                        currentVideo: positive[currentVideoIndex],
                        gags: gags[currentVideoIndex],
                    });
                } else {
                    // 
                    message({
                        content: (
                            <div className={styles.success}>
                                <div className={styles.successText}></div>
                            </div>
                        ),
                    });
                    changeListStates({
                        currentVideo: positive[0],
                        gags: gags[0],
                    });

                    // 
                    const error = new Error('positiveOfflineSource');

                    if (window && window.BJ_REPORT) window.BJ_REPORT.report(error, false, 45);
                }
            } else if (source === 'gags' && gagsId) {
                gags.forEach((item, index) => {
                    const currentIndex = item.findIndex(i => i.base62Id === gagsId);

                    if (currentIndex > -1) {
                        console.log('', positive[index]);
                        console.log('', item[currentIndex]);
                        console.log('', gags[index]);
                        changeListStates({
                            currentVideo: item[currentIndex],
                            gags: gags[index],
                        });
                    }
                });
            } else {
                changeListStates({
                    currentVideo: positive[0],
                    gags: gags[0],
                });
            }
        } else {
            console.log('');
            // 
            const history = getlocal('history');

            // 
            if (history) {
                // alert('');
                const dramaSeries = history.find(item => item.id === params.id);

                // 
                if (dramaSeries) {
                    // alert('');
                    // alert('');
                    const { videoSeries } = dramaSeries;

                    // 
                    videoSeries.sort((a, b) => {
                        return a.createTime < b.createTime ? 1 : -1;
                    });

                    const newVideoId = videoSeries[0].id;

                    // alert(newVideoId);

                    const index = positive.findIndex(item => item.base62Id === newVideoId);

                    if (index > -1) {
                        changeListStates({
                            currentVideo: positive[index],
                            gags: gags[index],
                        });
                    } else {
                        changeListStates({
                            currentVideo: positive[0],
                            gags: gags[0],
                        });
                    }
                } else {
                    // alert('');
                    // alert(positive[0].videoPlayUrl);
                    changeListStates({
                        currentVideo: positive[0],
                        gags: gags[0],
                    });
                }
            } else {
                // alert('');
                // 
                changeListStates({
                    currentVideo: positive[0],
                    gags: gags[0],
                });
            }
        }
    };

    // 
    useEffect(() => {
        // ios7440
        if (window.grounds && !window.grounds.getShowMode && window.grounds.on) {
            // 
            const nightMode = 'night';
            // 
            const showModeCallback = 'showModeCallback';

            window[showModeCallback] = current => {
                const isNightMode = current === nightMode;

                changeListStates({ isNightMode });
            };
            Grounds.on('theme', showModeCallback);
            // window.grounds.on('theme', showModeCallback);
        } else if (window.grounds && window.grounds.getShowMode && window.grounds.on) {
            // 
            const nightMode = 'night';
            const isNightMode = window.grounds.getShowMode() === nightMode;

            changeListStates({ isNightMode });
            // 
            const showModeCallback = 'showModeCallback';

            window[showModeCallback] = current => {
                const isNightMode = current === nightMode;

                changeListStates({ isNightMode });
            };
            Grounds.on('theme', showModeCallback);
            // window.grounds.on('theme', showModeCallback);
        }
    }, []);

    useEffect(() => {
        // 
        initAcount();
        initDramaSeries();
        initPositive();
        initRelationRecommend();
        initCrowd();
        initGags();
        initRecommend();
        // 
        inintVideo();
    }, []);

    // 
    const qqShareCofig = {
        title: dramaSeries.title,
        img: dramaSeries.posterUrl,
        desc: dramaSeries.summary,
        url: `${window.location.href}`,
        cb: () => {
            console.log('');
        },
    };

    return (
        <React.Fragment>
            <div className={styles.wrap}>{content.children}</div>
            {/* {qqShareCofig && qqShareCofig.title ? <QQShare content={qqShareCofig} /> : null} */}
        </React.Fragment>
    );
};

Wrap.propTypes = {
    // eslint-disable-next-line react/no-unused-prop-types
    content: PropTypes.object,
};

export default errorBoundary(Wrap);
